
.paginator {
    margin: 35px 10px;
    cursor: pointer;
    text-align: center;
}
.pageNumber {
    margin: 0 10px;
    padding: 2px 10px;
    border: 1px solid grey;
    border-radius: 5px

}

.pageNumber.selectedPage {
    font-weight: bold;
    border-color: black;
}
.paginator button{
    background: transparent;
    margin: 0 10px;
    padding: 3px 20px;
    border: 1px solid grey;
    border-radius: 5px;
    font-weight: 900;
    cursor: pointer
}